"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Edit, Trash2, Scissors, Clock, Euro } from "lucide-react"
import { useToast } from "@/hooks/use-toast"

interface Service {
  id: string
  name: string
  category: string
  duration: number // en minutes
  price: number
  description: string
  isActive: boolean
}

const mockServices: Service[] = [
  {
    id: "1",
    name: "Tresses Africaines",
    category: "Coiffure",
    duration: 180,
    price: 80,
    description: "Tresses africaines traditionnelles",
    isActive: true,
  },
  {
    id: "2",
    name: "Coupe Homme",
    category: "Coiffure",
    duration: 30,
    price: 25,
    description: "Coupe classique pour homme",
    isActive: true,
  },
  {
    id: "3",
    name: "Coloration",
    category: "Coloration",
    duration: 120,
    price: 65,
    description: "Coloration complète",
    isActive: true,
  },
  {
    id: "4",
    name: "Soin Profond",
    category: "Soins",
    duration: 45,
    price: 35,
    description: "Soin capillaire en profondeur",
    isActive: true,
  },
  {
    id: "5",
    name: "Défrisage",
    category: "Traitement",
    duration: 90,
    price: 55,
    description: "Défrisage complet",
    isActive: true,
  },
]

const categories = ["Coiffure", "Coloration", "Soins", "Traitement", "Tresses", "Locks"]

export function ServicesTab() {
  const { toast } = useToast()
  const [services, setServices] = useState<Service[]>(mockServices)
  const [editingId, setEditingId] = useState<string | null>(null)

  const [newService, setNewService] = useState({
    name: "",
    category: "",
    duration: "",
    price: "",
    description: "",
  })

  const handleAddService = () => {
    if (!newService.name || !newService.category || !newService.duration || !newService.price) {
      toast({
        title: "Erreur",
        description: "Veuillez remplir tous les champs obligatoires",
        variant: "destructive",
      })
      return
    }

    const service: Service = {
      id: Date.now().toString(),
      name: newService.name,
      category: newService.category,
      duration: Number.parseInt(newService.duration),
      price: Number.parseFloat(newService.price),
      description: newService.description,
      isActive: true,
    }

    setServices([...services, service])
    toast({
      title: "Prestation ajoutée",
      description: `${service.name} a été ajoutée avec succès`,
    })

    setNewService({ name: "", category: "", duration: "", price: "", description: "" })
  }

  const handleDeleteService = (id: string) => {
    setServices(services.filter((s) => s.id !== id))
    toast({ title: "Prestation supprimée" })
  }

  const handleToggleActive = (id: string) => {
    setServices(services.map((s) => (s.id === id ? { ...s, isActive: !s.isActive } : s)))
  }

  const formatDuration = (minutes: number) => {
    const hours = Math.floor(minutes / 60)
    const mins = minutes % 60
    if (hours > 0 && mins > 0) return `${hours}h${mins}`
    if (hours > 0) return `${hours}h`
    return `${mins}min`
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h2 className="text-2xl sm:text-3xl font-bold">Gestion des Prestations</h2>
        <p className="text-sm sm:text-base text-muted-foreground mt-1">
          {services.filter((s) => s.isActive).length} prestations actives sur {services.length}
        </p>
      </div>

      {/* Formulaire d'ajout */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Scissors className="w-5 h-5" />
            Enregistrer une nouvelle prestation
          </CardTitle>
          <CardDescription>Ajoutez un nouveau service à votre catalogue</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="sm:col-span-2">
              <Label htmlFor="name">Nom de la prestation *</Label>
              <Input
                id="name"
                value={newService.name}
                onChange={(e) => setNewService({ ...newService, name: e.target.value })}
                placeholder="Ex: Tresses Africaines"
              />
            </div>

            <div>
              <Label htmlFor="category">Catégorie *</Label>
              <Select value={newService.category} onValueChange={(v) => setNewService({ ...newService, category: v })}>
                <SelectTrigger id="category">
                  <SelectValue placeholder="Choisir..." />
                </SelectTrigger>
                <SelectContent>
                  {categories.map((cat) => (
                    <SelectItem key={cat} value={cat}>
                      {cat}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="duration">Durée (minutes) *</Label>
              <div className="relative">
                <Clock className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                <Input
                  id="duration"
                  type="number"
                  className="pl-10"
                  value={newService.duration}
                  onChange={(e) => setNewService({ ...newService, duration: e.target.value })}
                  placeholder="60"
                />
              </div>
            </div>

            <div>
              <Label htmlFor="price">Prix (€) *</Label>
              <div className="relative">
                <Euro className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                <Input
                  id="price"
                  type="number"
                  step="0.01"
                  className="pl-10"
                  value={newService.price}
                  onChange={(e) => setNewService({ ...newService, price: e.target.value })}
                  placeholder="50.00"
                />
              </div>
            </div>

            <div className="sm:col-span-2 lg:col-span-3">
              <Label htmlFor="description">Description</Label>
              <Textarea
                id="description"
                value={newService.description}
                onChange={(e) => setNewService({ ...newService, description: e.target.value })}
                placeholder="Description du service..."
                rows={2}
              />
            </div>

            <div className="flex items-end">
              <Button onClick={handleAddService} className="w-full bg-cyan-500 hover:bg-cyan-600">
                Enregistrer
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Liste des prestations */}
      <Card>
        <CardHeader>
          <CardTitle>Liste des prestations enregistrées</CardTitle>
          <CardDescription>Gérez vos services et tarifs</CardDescription>
        </CardHeader>
        <CardContent>
          {/* Version desktop */}
          <div className="hidden md:block overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Prestation</TableHead>
                  <TableHead>Catégorie</TableHead>
                  <TableHead>Durée</TableHead>
                  <TableHead>Prix</TableHead>
                  <TableHead>Statut</TableHead>
                  <TableHead className="text-right">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {services.map((service) => (
                  <TableRow key={service.id}>
                    <TableCell>
                      <div>
                        <p className="font-medium">{service.name}</p>
                        {service.description && <p className="text-sm text-muted-foreground">{service.description}</p>}
                      </div>
                    </TableCell>
                    <TableCell>
                      <Badge variant="outline">{service.category}</Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center gap-1">
                        <Clock className="w-4 h-4 text-muted-foreground" />
                        {formatDuration(service.duration)}
                      </div>
                    </TableCell>
                    <TableCell>
                      <span className="font-semibold">€{service.price.toFixed(2)}</span>
                    </TableCell>
                    <TableCell>
                      <Badge
                        variant={service.isActive ? "default" : "secondary"}
                        className={service.isActive ? "bg-green-500" : ""}
                      >
                        {service.isActive ? "Active" : "Inactive"}
                      </Badge>
                    </TableCell>
                    <TableCell className="text-right">
                      <div className="flex justify-end gap-2">
                        <Button variant="ghost" size="icon" onClick={() => handleToggleActive(service.id)}>
                          <Edit className="w-4 h-4" />
                        </Button>
                        <Button variant="ghost" size="icon" onClick={() => handleDeleteService(service.id)}>
                          <Trash2 className="w-4 h-4 text-destructive" />
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          {/* Version mobile */}
          <div className="md:hidden space-y-4">
            {services.map((service) => (
              <Card key={service.id}>
                <CardContent className="p-4">
                  <div className="flex justify-between items-start mb-3">
                    <div className="flex-1">
                      <h3 className="font-semibold text-lg">{service.name}</h3>
                      <Badge variant="outline" className="mt-1">
                        {service.category}
                      </Badge>
                    </div>
                    <Badge
                      variant={service.isActive ? "default" : "secondary"}
                      className={service.isActive ? "bg-green-500" : ""}
                    >
                      {service.isActive ? "Active" : "Inactive"}
                    </Badge>
                  </div>

                  {service.description && <p className="text-sm text-muted-foreground mb-3">{service.description}</p>}

                  <div className="flex items-center justify-between mb-3">
                    <div className="flex items-center gap-1 text-sm">
                      <Clock className="w-4 h-4 text-muted-foreground" />
                      <span>{formatDuration(service.duration)}</span>
                    </div>
                    <span className="text-xl font-bold">€{service.price.toFixed(2)}</span>
                  </div>

                  <div className="flex gap-2">
                    <Button
                      variant="outline"
                      size="sm"
                      className="flex-1 bg-transparent"
                      onClick={() => handleToggleActive(service.id)}
                    >
                      <Edit className="w-4 h-4 mr-2" />
                      Modifier
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      className="text-destructive bg-transparent"
                      onClick={() => handleDeleteService(service.id)}
                    >
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
